import { Box, Text, Heading, Flex } from "@chakra-ui/react";
import {
  Chart,
  ChartComponent,
  GlyphSize,
  MotifColor,
  motifColors,
} from "@tanyoknits/shared";
import { css, cx } from "@linaria/core";
import { MotifRepeat } from "../utils/types";

export interface MotifRepeatsProps {
  chart: Chart;
  motifList: MotifRepeat[];
  size?: GlyphSize;
}

const styles = {
  box: css`
    border-width: 2px;
  `,
};

export default function MotifRepeatsPattern({
  chart,
  motifList,
  size = "M",
}: MotifRepeatsProps) {
  return (
    <Flex direction="column" gap={4}>
      <Heading size="md">Motif repeats</Heading>
      <Flex gap={2} flexWrap="wrap">
        <Text whiteSpace="nowrap">
          Total <strong>{chart.length}</strong> rows:
        </Text>
        <Flex alignItems="center" gap={2} flexWrap="wrap">
          {motifList.map((m, i) => (
            <Flex key={i} gap={2} alignItems="center">
              <Box
                width={4}
                height={4}
                className={cx(styles.box, motifColors[m.id as MotifColor])}
              />
              <Text>
                {m.title != null ? `${m.title}: ` : ""}
                <strong>{chart.length / m.times}</strong> rows x{` `}
                <strong>{m.times}</strong> time{m.times > 1 ? "s" : ""}
              </Text>
            </Flex>
          ))}
        </Flex>
      </Flex>

      <Box width="100%" overflowX="auto">
        <ChartComponent chart={chart} size={size} direction="flat" />
      </Box>
    </Flex>
  );
}
