import { getRound } from "@tanyoknits/shared";
import { setInSleevePulloverMeasurement } from "../../utils/sizeBase";
import { getCoreGaugeItems } from "./common";
import { SetInSleevePulloverGaugeProps } from "./types";

export function getGaugeItems(
  ms: setInSleevePulloverMeasurement,
  stsGauge: number,
  rowGauge: number,
  hasOddSts: boolean = false,
  adjHalfBody?: number
): SetInSleevePulloverGaugeProps {
  const coreItems = getCoreGaugeItems(
    ms,
    stsGauge,
    rowGauge,
    hasOddSts,
    adjHalfBody
  );

  return {
    ...coreItems,
    sleeveLength: getRound(ms.sleeve_length_from_underarm * rowGauge),
    cuff: getRound(ms.cuff * stsGauge),
    wrist: getRound(ms.wrist * stsGauge),
  };
}

export function getTotalSts(
  ms: setInSleevePulloverMeasurement,
  stsGauge: number,
  rowGauge: number
): number {
  const bodyRect =
    ms.total_length * rowGauge * ms.bust_circumference * stsGauge;

  // neck triangle
  const neck = (ms.neck_back_depth * rowGauge * ms.neck_width * stsGauge) / 2;

  // super rough estimate of sleeve triangle
  const sleeve =
    (ms.sleeve_length_from_underarm *
      rowGauge *
      ms.armhole_depth *
      2 *
      stsGauge) /
    2;

  return bodyRect - neck + sleeve * 2;
}
