import { Box, Flex } from "@chakra-ui/react";
import { useState } from "react";
import DisplayOptions from "./DisplayOptions";
import {
  ChartComponent,
  ChartProps,
  PlainTextList,
  Written,
  WrittenDisplayProps,
  MultiLevelTextList,
} from "@tanyoknits/shared";
import { PatternPartInstruction } from "../utils/types";
import COInstruction from "./COInstruction";

interface Props {
  chart: ChartProps;
  instruction: PatternPartInstruction;
}

export default function StaticPattern({ chart, instruction }: Props) {
  const [menu, setMenu] = useState<string>("1");
  const writtenDisplay: WrittenDisplayProps = menu === "1" ? "inline" : "block";
  const { co, details, beforeList, afterList } = instruction;

  return (
    <Flex direction="column" gap={4}>
      {co != null && <COInstruction {...co} />}
      {details != null && <MultiLevelTextList list={details} />}
      {beforeList != null && <PlainTextList list={beforeList} />}
      {chart.chart.length > 0 && (
        <Flex flexWrap="wrap" gap={6}>
          <Box overflowX="auto">
            <ChartComponent {...chart} />
          </Box>

          {
            <Flex direction="column" gap={4}>
              <DisplayOptions
                menu={menu}
                setMenu={setMenu}
                options={["Inline", "Split by motif"]}
              />
              <Written {...chart} display={writtenDisplay} />
            </Flex>
          }
        </Flex>
      )}
      {afterList != null && <PlainTextList list={afterList} />}
    </Flex>
  );
}
