import { ChartComponent, ChartProps } from "@tanyoknits/shared";
import { Box, Divider, Flex } from "@chakra-ui/react";
import PatternPartToggle from "./PatternPartToggle";
import React from "react";
import { PatternPartGroup, PatternPartInstruction } from "../utils/types";
import StaticPattern from "./StaticPattern";
import InteractivePattern from "./InteractivePattern";

export default function PatternPartsGroup({
  overviewChart,
  parts,
  partStartID = 0,
}: PatternPartGroup) {
  return (
    <Flex direction="column" gap={4}>
      {overviewChart != null && (
        <>
          <PatternPartToggle title="Combined chart" isOpenDefault={true}>
            <Box width="100%" overflow="auto">
              <ChartComponent chart={overviewChart} size="S" />
            </Box>
          </PatternPartToggle>
          <Divider variant="dashed" my={2} />
        </>
      )}
      {parts.map((part, i) => {
        const { chart, direction, size, startRowID }: ChartProps = part;
        const chartProps = { chart, direction, size, startRowID };
        const { co, details, beforeList, afterList }: PatternPartInstruction =
          part;
        const instructionProps: PatternPartInstruction = {
          co,
          details,
          beforeList,
          afterList,
        };
        return (
          <React.Fragment key={i}>
            <PatternPartToggle
              key={i}
              partNum={partStartID + i + 1}
              title={part.instruction}
              isOpenDefault={true}
            >
              {part.isOnlyRowClickable ? (
                <InteractivePattern
                  chart={chartProps}
                  instruction={instructionProps}
                />
              ) : (
                <StaticPattern
                  chart={chartProps}
                  instruction={instructionProps}
                />
              )}
            </PatternPartToggle>
            {i < parts.length - 1 && <Divider variant="dashed" my={2} />}
          </React.Fragment>
        );
      })}
    </Flex>
  );
}
