import {
  Size,
  FOSizing,
  baseFOSizing,
  FOPart,
  baseBodySizing,
  setInSleevePulloverMeasurement,
  setInSleevePulloverCoreParts,
  SetInSleevePulloverCorePart,
} from "../../utils/sizeBase";
import { getIntroSizingInfo } from "../../utils/misc";
import { IntroSizing } from "../../utils/types";
import profile from "./profile";
import { getTotalSts } from "../../patternMakers/setInSleeve/pullover";

export const sizes: Size[] = ["L"];

const baseMeasurement: FOSizing = {
  L: {
    ...baseFOSizing.L,
    neck_width: 8,
    shoulder_width: 5.5,
    underarm: 1.25,
    neck_circumference: 18,
    bust_circumference: 42,
    under_arm_circumference: 19,
    armhole_depth: 10.5,
    neck_depth: 2.5,
    total_length: 26,
    wrist_circumference: 8,
    sleeve_length_from_underarm: 21,
    neck_band_height: 8,
    hem: 2.25,
  },
};

export const measurements: Partial<Record<FOPart, number>>[] = sizes.map(
  (size) => {
    const ms = baseMeasurement[size] ?? {};
    return Object.keys(ms)
      .filter((key) =>
        setInSleevePulloverCoreParts.includes(
          key as SetInSleevePulloverCorePart
        )
      )
      .reduce((cur, key) => {
        return Object.assign(cur, {
          [key]: ms?.[key as SetInSleevePulloverCorePart],
        });
      }, {});
  }
);

const totalStsBySize: number[] = measurements.map((m, i) => {
  const schema = getTotalSts(
    m as setInSleevePulloverMeasurement,
    profile.gauge.sts_count,
    profile.gauge.row_count
  );

  return schema;
});

export const introSizing: IntroSizing = getIntroSizingInfo(
  sizes,
  baseBodySizing,
  measurements,
  totalStsBySize,
  ["bust"],
  ["bust_circumference", "sleeve_length_from_underarm", "total_length"],
  // TODO: add final weight later
  { size: "L", grams: 600 }
);
