/** Motif repeats on Overview page */

import {
  Chart,
  MotifColor,
  getChartFromMotifs,
  getChartFromText,
  getMergedChart,
  getRightLeftFlippedChart,
  getRound,
  getStsCountFromChartRow,
} from "@tanyoknits/shared";
import { Size, VestMeasurement } from "../../utils/sizeBase";
import { measurements, sizes } from "./sizes";
import { bigBraid } from "./stitches";
import { getMotifBySize } from "../../utils/misc";
import profile from "./profile";

// Gutters
const P2 = "p:2";
const gutters: Partial<Record<Size, string[]>> = {
  L: [P2, P2],
};

export function getMotifChartBySize(sizeID: number, armInset: number): Chart {
  const size = sizes[sizeID];
  const motif = {
    ...getMotifBySize(bigBraid, size),
    id: "red" as MotifColor,
  };

  // Get St equivalent count of motif
  const side = `k:${armInset + 1}`;
  const gutter: string[] = gutters[size] as string[];
  const chartOnRight = getChartFromMotifs([
    side,
    gutter[0],
    motif,
    gutter[1],
    side,
  ]);
  const chartOnLeft = getRightLeftFlippedChart(chartOnRight);
  const motifChartFront = getMergedChart([chartOnRight, chartOnLeft]);
  const totalStEquivCount = getStsCountFromChartRow(motifChartFront[0], true);
  const totalStsCOunt = getStsCountFromChartRow(motifChartFront[0], false);

  // Get additional sts count needed
  const ms = measurements[sizeID] as VestMeasurement;
  const width = ms.bust_circumference / 2;
  const stsGauge = profile.gauge.sts_count / 4;
  const halfBodyStsCount = getRound(width * stsGauge);
  const noMotifStsCount = halfBodyStsCount - totalStsCOunt;

  // Get K chart
  const kChart = getChartFromText([
    `k:${noMotifStsCount + (totalStsCOunt - totalStEquivCount)}`,
  ]);

  return getMergedChart([chartOnRight, kChart, chartOnLeft]);
}
