import { SetInSleeveSchema, getChartFromText } from "@tanyoknits/shared";
import { pulloverPartsInstruction } from "../../patternMakers/setInSleeve";
import OverviewWithSchema from "../../components/OverviewWithSchema";
import PageContentWrapper from "../../layout/PageContentWrapper";
import { PatternBySizeProps } from "../../layout/PatternBySize";
import { getTopDownNotes } from "../../patternMakers/text";
import profile from "./profile";
import { setInSleevePulloverMeasurement } from "../../utils/sizeBase";
import { getGaugeItems } from "../../patternMakers/setInSleeve/pullover";
import { measurements } from "./sizes";
import { SetInSleevePulloverGaugeProps } from "../../patternMakers/setInSleeve/types";
import PatternPartsGroup from "../../components/PatternGroupedParts";
import { getPatternByPartGroup } from "../../patternMakers/setInSleeve/common";
import { getMotifChartBySize } from "./motifs";
import MotifRepeatsPattern from "../../components/MotifRepeatsPattern";
import { Divider } from "@chakra-ui/react";

export default function Instruction({ menu, size }: PatternBySizeProps) {
  // size
  const stsGauge = profile.gauge.sts_count / 4;
  const rowGauge = profile.gauge.row_count / 4;
  const ms = measurements[size] as setInSleevePulloverMeasurement;
  const gaugeItems: SetInSleevePulloverGaugeProps = getGaugeItems(
    ms,
    stsGauge,
    rowGauge,
    true
  );

  // create front and back chart
  const { halfBody, armInset } = gaugeItems;
  const frontChart = getMotifChartBySize(size, armInset);
  const backChart = getChartFromText([`k:${halfBody}`]);

  const [back, front, body, sleeve, band] = getPatternByPartGroup(
    "pullover",
    backChart,
    frontChart,
    stsGauge,
    rowGauge,
    gaugeItems,
    profile.needles[0].size,
    profile.needles[1].size,
    4
  );

  const props = [
    back,
    { ...front, partStartID: 3 },
    { ...body, partStartID: 6 },
    { ...sleeve, partStartID: 7 },
    { ...band, partStartID: 9 },
  ];

  return (
    <PageContentWrapper>
      {menu === 0 ? (
        <>
          <OverviewWithSchema
            overview={getTopDownNotes("sweater")}
            parts={pulloverPartsInstruction}
          >
            <SetInSleeveSchema viewBoxRatio={1.6} neck="round" />
          </OverviewWithSchema>
          <Divider variant="dashed" />
          <MotifRepeatsPattern chart={frontChart} motifList={[]} size="S" />
        </>
      ) : (
        <PatternPartsGroup {...props[menu - 1]} />
      )}
    </PageContentWrapper>
  );
}
